* {
  box-sizing: border-box;
  font-family: "Libre";
  font-weight: 400;
  font-style: normal;
  color: rgb(94, 89, 98);
}

.allApp {
  display: flex;
  justify-content: center;
}

.allApp__app {
  background-color: rgb(255, 255, 255);
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 150px;
  width: 435px;
}

@media screen and (max-width: 437px) {
.allApp__app {
  width: 90vw;
}
}